module.exports = function(form) {
  var data = {};
  for (var el of [...form.elements]) {
    if (el.disabled || !el.name || el.type === 'file') {
      // ignore disabled inputs
      // ignore inputs without name attributes
      // ignore file inputs
      continue;
    } else if (['checkbox', 'radio'].includes(el.type) && !el.checked) {
      // ignore unchecked radio/checkbox inputs
      continue;
    } else if (el.value) {
      // set truthy values
      data[el.name] = el.value;
    } else if (data[el.name]) {
      // falsy values delete a previously set value
      // e.g. a checked checkbox with value="" overrides an earlier hidden input
      delete data[el.name];
    }
  }
  return data;
};
